e: 14px;\n    font-weight: 500;\n    margin-right: 22px;\n    cursor: pointer;\n}\n.import-btn[data-v-37d6c4e5] {\n    background: none;\n    border: none;\n    font-size: 14px;\n    font-weight: 500;\n    color: #2C5A86;\n    cursor: pointer;\n}\n.add-ref-module-btn[data-v-37d6c4e5] {\n    font-size: 13px;\n    text-align: center;\n    color: #FFFFFF;\n    background: #151233;\n    display: flex;\n    align-items: center;\n    padding: 4px 8px;\n    border-radius: 4px;\n    border: none;\n}\n.remove-module:hover .button-tooltip[data-v-37d6c4e5] {\n    opacity: 1;\n    visibility: visible;\n}\n.button-tooltip[data-v-37d6c4e5] {\n    visibility: hidden;\n    position: absolute;\n    background: #757575;\n    color: #FFFFFF;\n    font-size: 9px;\n    font-family: 'Roboto';\n    text-align: center;\n    border-radius: 4px;\n    letter-spacing: 0.2px;\n    padding: 6px 8px 6px 8px;\n    opacity: 0;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/dialogs/attachModuleToApp.vue"],"names":[],"mappings":";AAyWA;IACI,gBAAgB;AACpB;AAEA;IACI,eAAe;IACf;AACJ;AAEA;IACI,kBAAkB;IAClB,WAAW;AACf;AAEA;IACI,uBAAuB;IACvB,aAAa;IACb,cAAc;IACd,mBAAmB;AACvB;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uDAAuD;AAC3D;AAEA;IACI,YAAY;IACZ,4BAA4B;IAC5B,mBAAmB;IACnB,eAAe;IACf,gBAAgB;IAChB,iBAAiB;IACjB,mBAAmB;IACnB,kBAAkB;IAClB,cAAc;AAClB;AAEA;IACI,cAAc;IACd,wBAAwB;IACxB,mBAAmB;IACnB,eAAe;IACf,gBAAgB;IAChB,iBAAiB;IACjB,mBAAmB;IACnB,kBAAkB;AACtB;AAEA;IACI,uBAAuB;AAC3B;AAEA;IACI,cAAc;IACd,eAAe;IACf,gBAAgB;IAChB,iBAAiB;IACjB,mBAAmB;AACvB;AAEA;IACI,YAAY;IACZ,gBAAgB;IAChB,cAAc;IACd,eAAe;AACnB;AAEA;IACI,eAAe;IACf,cAAc;IACd,eAAe;IACf,kBAAkB;AACtB;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,mBAAmB;AACvB;AAEA;IACI,cAAc;IACd,eAAe;IACf,gBAAgB;AACpB;AAEA;IACI,gBAAgB;IAChB,YAAY;IACZ,eAAe;IACf,cAAc;IACd,WAAW;IACX,aAAa;IACb,oBAAoB;IACpB,mBAAmB;AACvB;AAEA;IACI,cAAc;IACd,eAAe;IACf,gBAAgB;IAChB,kBAAkB;IAClB,eAAe;AACnB;AAEA;IACI,gBAAgB;IAChB,YAAY;IACZ,eAAe;IACf,gBAAgB;IAChB,cAAc;IACd,eAAe;AACnB;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,cAAc;IACd,mBAAmB;IACnB,aAAa;IACb,mBAAmB;IACnB,gBAAgB;IAChB,kBAAkB;IAClB,YAAY;AAChB;AAEA;IACI,UAAU;IACV,mBAAmB;AACvB;AAEA;IACI,kBAAkB;IAClB,kBAAkB;IAClB,mBAAmB;IACnB,cAAc;IACd,cAAc;IACd,qBAAqB;IACrB,kBAAkB;IAClB,kBAAkB;IAClB,qBAAqB;IACrB,wBAAwB;IACxB,UAAU;AACd","sourcesContent":["<script setup lang=\"ts\">\nimport { ref } from 'vue';\nimport { container } from '../../../../domain/core/diContainer';\nimport { ILocalization } from '../../../../domain/infrastructure/ILocalization';\nimport { IModule, IModuleVersion, IExtendedModuleItem } from '../../../../domain/model/models';\nimport { IModuleVersionUseCase } from '../../../../domain/useCase/IModuleVersionUseCase';\nimport { IObjectUseCase } from '../../../../domain/useCase/IObjectUseCase';\nimport { IStudio } from '../../../../domain/useCase/IStudio';\nimport { IUseCaseExecutor } from '../../../../domain/useCase/IUseCaseExecutor';\nimport { IViewModel } from '../../../../domain/viewModel/IViewModel';\n\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst localization = container.resolve<ILocalization>(ILocalization);\nconst objectUseCase = container.resolve<IObjectUseCase>(IObjectUseCase);\nconst studio = container.resolve<IStudio>(IStudio);\nconst moduleVersionUseCase = container.resolve<IModuleVersionUseCase>(IModuleVersionUseCase);\n\nconst importedOrgModulesData = ref<Array<IExtendedModuleItem>>([]);\nconst importedGroupModulesData = ref<Array<IExtendedModuleItem>>([]);\nconst avaliableOrgModulesData = ref<Array<IExtendedModuleItem>>([]);\nconst avaliableGroupModulesData = ref<Array<IExtendedModuleItem>>([]);\nconst importNewOrChange = ref(false);\nconst selectedModule = ref<IExtendedModuleItem>();\nconst moduleVersions = ref<Array<IModuleVersion>>([]);\n\nconst tableColumns = ref([\"name\", \"description\", \"ownerApp\", \"relatedAppNames\", \"createdBy\", \"createDate\", \"actions\"]);\nconst tableOptions = ref({\n    filterable: true,\n    perPage: 5,\n    perPageValues: [],\n    headings: {\n        name: localization.get(\"moduleName\"),\n        description: localization.get(\"description\"),\n        ownerApp: localization.get(\"ownerApp\"),\n        relatedAppNames: localization.get(\"relatedApplications\"),\n        createdBy: localization.get(\"createdBy\"),\n        createDate: localization.get(\"createDate\"),\n        shareType: localization.get(\"shareType\"),\n        actions: \"\",\n    },\n    resizableColumns: false,\n    skin: 'import-module',\n    texts: {\n        count: \"\",\n        noResults: \"No matching records\",\n        filterPlaceholder: \"Search\"\n    },\n});\nconst moduleVersionTableColumns = ref([\"version\", \"description\", \"createdBy\", \"createDate\", \"state\"]);\nconst importedModuleType = ref<\"Organization\" | \"Group\">(\"Organization\");\nconst availableModule = ref<\"Organization\" | \"Group\">(\"Organization\");\nconst orgGroup = ref(viewModel.user!.selectedOrganizationGroup?.organizationGroup)\nconst mainAppModules = ref(viewModel.user!.selectedOrganizationGroup?.organizationApplications?.find(app => app.organizationId == orgGroup.value?.mainOrganizationId)?.modules)\n\nconst moduleVersionTableOptions = ref({\n    filterable: false,\n    perPage: 5,\n    perPageValues: [],\n    headings: {\n        version: localization.get(\"version\"),\n        description: localization.get(\"description\"),\n        createdBy: localization.get(\"createdBy\"),\n        createDate: localization.get(\"createDate\"),\n        state: \"\",\n    },\n    resizableColumns: false,\n    skin: 'module-versions',\n    texts: {\n        count: \"\",\n        noResults: \"No matching records\",\n    },\n})\nconst dateOptions: Intl.DateTimeFormatOptions = { month: '2-digit', day: '2-digit', year: 'numeric' };\n\nfunction refreshTable() {\n    executor.execute(async () => {\n        const modules: Array<IExtendedModuleItem> = await objectUseCase.listModules();\n        const groupModules = await objectUseCase.getGroupModules();\n        const appsDict: Record<string, string> = {};\n        viewModel.apps.forEach(app => appsDict[app.ID] = app.name);\n\n        modules.forEach(module => {\n            setOwnerApp(module, appsDict);\n            const isAddedtoApp = isAddedtoCurrentApp(module);\n            if (isAddedtoApp && module.mainOwner && module.mainOwner != viewModel.studio.appId) {\n                !importedOrgModulesData.value.some(data => data.ID == module.ID) && importedOrgModulesData.value.push(module);\n                avaliableOrgModulesData.value = avaliableOrgModulesData.value.filter(data => data.ID != module.ID);\n            } else if (!isAddedtoApp) {\n                !avaliableOrgModulesData.value.some(data => data.ID == module.ID) && avaliableOrgModulesData.value.push(module);\n                importedOrgModulesData.value = importedOrgModulesData.value.filter(data => data.ID != module.ID);\n            }\n        });\n        groupModules?.forEach(module => {\n            setOwnerApp(module, appsDict);\n            const isAddedtoApp = isAddedtoCurrentApp(module);\n            if (isAddedtoApp) {\n                !importedGroupModulesData.value.some(data => data.ID == module.ID) && importedGroupModulesData.value.push(module);\n                avaliableGroupModulesData.value = avaliableGroupModulesData.value.filter(data => data.ID != module.ID);\n            } else {\n                !avaliableGroupModulesData.value.some(data => data.ID == module.ID) && avaliableGroupModulesData.value.push(module);\n                importedGroupModulesData.value = importedGroupModulesData.value.filter(data => data.ID != module.ID);\n            }\n        })\n    }, { loading: true });\n\n    function setOwnerApp(module: IExtendedModuleItem, appsDict: Record<string, string>) {\n        module.ownerApp = module.mainOwner ? appsDict[module.mainOwner] : '';\n        const appNamesArr = module.relatedApplications.map(i => appsDict[i.applicationID]).filter(appName => appName != module.ownerApp);\n        module.relatedAppNames = appNamesArr.join(\", \");\n    }\n}\n\nrefreshTable()\nfunction add(module: IModule, version?: string) {\n    const moduleFromGroup = mainAppModules.value?.find(appModule => appModule.ID == module.ID);\n    const applicationID = viewModel.studio.appId;\n    const importedData = moduleFromGroup ? importedGroupModulesData : importedOrgModulesData;\n    const availableModules = moduleFromGroup ? avaliableGroupModulesData : avaliableOrgModulesData;\n    const mainOrgId = moduleFromGroup ? orgGroup.value?.mainOrganizationId : undefined;\n    executor.execute(async () => {\n        if (version) {\n            if (isAddedtoCurrentApp(module)) {\n                await studio.detachModuleFromApplication(module, applicationID, true, mainOrgId);\n                const curModule = importedData.value.find(i => i.ID == module.ID)!;\n                curModule.relatedApplications = curModule.relatedApplications.filter(i => i.applicationID != applicationID);\n            }\n            await studio.attachModuletoApplication(module, applicationID, true, version, mainOrgId);\n            const curModule = availableModules.value.find(i => i.ID == module.ID);\n            curModule!.relatedApplications.push({ applicationID, importedVersion: version });\n            const curModuleVersion = moduleVersions.value.find(modVersion => modVersion.version == version);\n            curModuleVersion!.relatedApplications!.push({ applicationID, importedVersion: version });\n            selectedModule.value!.currentVersion = version;\n        }\n        else {\n            await studio.attachModuletoApplication(module, applicationID, false, mainOrgId);\n            const curModule = availableModules.value.find(i => i.ID == module.ID);\n            curModule!.relatedApplications.push({ applicationID });\n        }\n        refreshTable()\n    }, { loading: true });\n}\n\nfunction remove(module: IModule, version?: string) {\n    const moduleFromGroup = mainAppModules.value?.find(appModule => appModule.ID == module.ID);\n    const importedData = moduleFromGroup ? importedGroupModulesData : importedOrgModulesData;\n    const mainOrgId = moduleFromGroup ? orgGroup.value?.mainOrganizationId : undefined;\n    executor.execute(async () => {\n        const applicationID = viewModel.studio.appId;\n        await studio.detachModuleFromApplication(module, applicationID, false, version, mainOrgId);\n        const curModule = importedData.value.find(i => i.ID == module.ID)!;\n        curModule.relatedApplications = curModule.relatedApplications.filter(i => i.applicationID != applicationID);\n        const curModuleVersion = moduleVersions.value.find(modVersion => modVersion.version == version);\n        if(curModuleVersion) { curModuleVersion.relatedApplications = curModule.relatedApplications.filter(i => i.applicationID != applicationID); }\n        refreshTable()\n    }, { loading: true });\n}\n\nfunction isAddedtoCurrentApp(module: IModule) {\n    const appID = viewModel.studio.appId;\n    const isAdded = module.relatedApplications?.some(i => i.applicationID == appID);\n    return !!isAdded;\n}\n\nfunction selectModule(module: IModule) {\n    getVersions(module.ID);\n    selectedModule.value = module;\n    selectedModule.value.currentVersion = getImportedVersion(module);\n    importNewOrChange.value = true;\n}\nfunction getVersions(moduleID: string) {\n    executor.execute(async () => {\n        const moduleFromGroup = mainAppModules.value?.find(appModule => appModule.ID == moduleID);\n        const mainOrgId = moduleFromGroup ? orgGroup.value?.mainOrganizationId : undefined;\n        moduleVersions.value = await moduleVersionUseCase.listModuleVersions(moduleID, mainOrgId);\n    }, { loading: true })\n}\nfunction getImportedVersion(module: IModule) {\n    const applicationID = viewModel.studio.appId;\n    const importedVersion = module.relatedApplications.find(app => applicationID == app.applicationID)?.importedVersion;\n    return importedVersion;\n}\nfunction computeImportedVersion() {\n    return selectedModule.value?.currentVersion;\n}\nfunction goBack() {\n    importNewOrChange.value = false;\n    // refreshTable();\n}\n\n</script>\n\n<template>\n    <div class=\"container vue-table\">\n        <div v-if=\"!importNewOrChange\">\n            <div class=\"module-tabs\">\n                <div @click=\"importedModuleType = 'Organization'\"\n                    :class=\"['tab-item', importedModuleType == 'Organization' ? 'active-category' : '']\">Imported\n                    Organization Modules\n                </div>\n                <div @click=\"importedModuleType = 'Group'\"\n                    :class=\"['tab-item', importedModuleType == 'Group' ? 'active-category' : '']\">\n                    Imported\n                    Organization Group Modules</div>\n            </div>\n            <div v-if=\"importedModuleType == 'Organization'\">\n                <div v-if=\"importedOrgModulesData.length > 0\" class=\"module-tab-div\">\n                    <v-client-table ref=\"table\" :data=\"importedOrgModulesData\" :columns=\"tableColumns\"\n                        :options=\"tableOptions\">\n                        <template #createDate=\"props\">\n                            <span>{{ new Intl.DateTimeFormat(\"tr-TR\", dateOptions).format(new Date(props.row.createDate)) }}</span>\n                        </template>\n                        <template #ownerApp=\"props\">\n                            <span>{{ props.row.ownerApp }}</span>\n                        </template>\n                        <template #relatedAppNames=\"props\">\n                            <span>{{ props.row.relatedAppNames }}</span>\n                        </template>\n                        <template #actions=\"props\">\n                            <div style=\"display: flex; align-items: center;\">\n                                <div v-show=\"getImportedVersion(props.row)\" class=\"imported-version\"\n                                    @click=\"selectModule(props.row)\">\n                                    <i class=\"mdi mdi-pencil\" style=\"font-size: 16px; margin-right: 4px\"></i>\n                                    <span>{{ getImportedVersion(props.row) }}</span>\n                                </div>\n                                <button class=\"remove-module\" @click=\"remove(props.row, getImportedVersion(props.row))\">\n                                    <i class=\"mdi mdi-close-circle-outline\"></i>\n                                    <span class=\"button-tooltip\" style=\"right: 48px; z-index: 1; width: 80px;\">Remove\n                                        Module</span>\n                                </button>\n                            </div>\n                        </template>\n                    </v-client-table>\n                </div>\n                <div v-else class=\"no-data-div\">\n                    <img class=\"ghost-img\" src=\"../../../../images/ghost.svg\" />\n                    <span style=\"font-size: 14px; color: #757575\">No modules imported yet.</span>\n                </div>\n            </div>\n            <div v-else>\n                <div v-if=\"importedGroupModulesData.length > 0\" class=\"module-tab-div\">\n                    <v-client-table ref=\"table\" :data=\"importedGroupModulesData\" :columns=\"tableColumns\"\n                        :options=\"tableOptions\">\n                        <template #createDate=\"props\">\n                            <span>{{ new Intl.DateTimeFormat(\"tr-TR\", dateOptions).format(new Date(props.row.createDate)) }}</span>\n                        </template>\n                        <template #ownerApp=\"props\">\n                            <span>{{ props.row.ownerApp }}</span>\n                        </template>\n                        <template #relatedAppNames=\"props\">\n                            <span>{{ props.row.relatedAppNames }}</span>\n                        </template>\n                        <template #actions=\"props\">\n                            <div style=\"display: flex; align-items: center;\">\n                                <div v-show=\"getImportedVersion(props.row)\" class=\"imported-version\"\n                                    @click=\"selectModule(props.row)\">\n                                    <i class=\"mdi mdi-pencil\" style=\"font-size: 16px; margin-right: 4px\"></i>\n                                    <span>{{ getImportedVersion(props.row) }}</span>\n                                </div>\n                                <button class=\"remove-module\" @click=\"remove(props.row, getImportedVersion(props.row))\">\n                                    <i class=\"mdi mdi-close-circle-outline\"></i>\n                                    <span class=\"button-tooltip\" style=\"right: 48px; z-index: 1; width: 80px;\">Remove\n                                        Module</span>\n                                </button>\n                            </div>\n                        </template>\n                    </v-client-table>\n                </div>\n                <div v-else class=\"no-data-div\">\n                    <img class=\"ghost-img\" src=\"../../../../images/ghost.svg\" />\n                    <span style=\"font-size: 14px; color: #757575\">No modules imported yet.</span>\n                </div>\n            </div>\n            <div class=\"module-tabs\">\n                <div @click=\"availableModule = 'Organization'\"\n                    :class=\"['tab-item', availableModule == 'Organization' ? 'active-category' : '']\">\n                    Organization Modules\n                </div>\n                <div @click=\"availableModule = 'Group'\"\n                    :class=\"['tab-item', availableModule == 'Group' ? 'active-category' : '']\">\n                    Organization Group Modules</div>\n            </div>\n            <div v-if=\"availableModule == 'Organization'\">\n                <div v-if=\"avaliableOrgModulesData.length > 0\" class=\"module-tab-div\">\n                    <v-client-table ref=\"table\" :data=\"avaliableOrgModulesData\" :columns=\"tableColumns\"\n                        :options=\"tableOptions\">\n                        <template #createDate=\"props\">\n                            <span>{{ new Intl.DateTimeFormat(\"tr-TR\", dateOptions).format(new Date(props.row.createDate)) }}</span>\n                        </template>\n                        <template #ownerApp=\"props\">\n                            <span>{{ props.row.ownerApp }}</span>\n                        </template>\n                        <template #relatedAppNames=\"props\">\n                            <span>{{ props.row.relatedAppNames }}</span>\n                        </template>\n                        <template #actions=\"props\">\n                            <button v-show=\"props.row.shareType == 'version'\" class=\"select-module-btn\"\n                                @click=\"selectModule(props.row)\"><i class=\"mdi mdi-chevron-right\"></i></button>\n                            <button v-show=\"props.row.shareType == 'reference'\" class=\"add-ref-module-btn\"\n                                @click=\"add(props.row)\">ADD</button>\n                        </template>\n                    </v-client-table>\n                </div>\n                <div v-else class=\"no-data-div\">\n                    <img class=\"ghost-img\" src=\"../../../../images/ghost.svg\" />\n                    <span class=\"no-module-text\">No available module yet.</span>\n                </div>\n            </div>\n            <div v-else class=\"module-tab-div\">\n                <div v-if=\"avaliableGroupModulesData.length > 0\" class=\"module-tab-div\">\n                    <v-client-table ref=\"table\" :data=\"avaliableGroupModulesData\" :columns=\"tableColumns\"\n                        :options=\"tableOptions\">\n                        <template #createDate=\"props\">\n                            <span>{{ new Intl.DateTimeFormat(\"tr-TR\", dateOptions).format(new Date(props.row.createDate)) }}</span>\n                        </template>\n                        <template #ownerApp=\"props\">\n                            <span>{{ props.row.ownerApp }}</span>\n                        </template>\n                        <template #relatedAppNames=\"props\">\n                            <span>{{ props.row.relatedAppNames }}</span>\n                        </template>\n                        <template #actions=\"props\">\n                            <button v-show=\"props.row.shareType == 'version'\" class=\"select-module-btn\"\n                                @click=\"selectModule(props.row)\"><i class=\"mdi mdi-chevron-right\"></i></button>\n                            <button v-show=\"props.row.shareType == 'reference'\" class=\"add-ref-module-btn\"\n                                @click=\"add(props.row)\">ADD</button>\n                        </template>\n                    </v-client-table>\n                </div>\n                <div v-else class=\"no-data-div\">\n                    <img class=\"ghost-img\" src=\"../../../../images/ghost.svg\" />\n                    <span class=\"no-module-text\">No available module yet.</span>\n                </div>\n            </div>\n        </div>\n        <div v-if=\"importNewOrChange\">\n            <div class=\"module-name-div\">\n                <i class=\"mdi mdi-arrow-left-box back-button\" @click=\"goBack()\"></i>\n                <span class=\"module-name\">{{ selectedModule?.name }}</span>\n            </div>\n            <div>\n                <span style=\"color: #212121; font-size: 14px\">Choose the module version you want to import.</span>\n            </div>\n            <v-client-table ref=\"table\" :data=\"moduleVersions\" :columns=\"moduleVersionTableColumns\"\n                :options=\